import React, { useEffect } from 'react'
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import './doc.css';


const DocContent = (props) => {
    return <h1>
        { props.match.params.type }
    </h1>
}
const Doc = (props) => {
    console.log(props)

    return (
        <div className='doc'>
            <div className="content">
                <Switch>
                    {props.routes.map((route, i) => (
                        <RouteWithSubRoutes key={i} {...route} />
                    ))}
 		        </Switch>
                <Redirect from='/doc' to='/doc/core' />
            </div>
            <ol>
                <li><NavLink exact to='/doc/core'>核心概念</NavLink></li>
                <li><NavLink to='/doc/guide'>高级指引</NavLink></li>
                <li><NavLink to='/doc/api'>API</NavLink></li>
                <li><NavLink to='/doc/hooks'>Hooks</NavLink></li>
            </ol>
        </div>
    )
}
function RouteWithSubRoutes(route) {
	return (
	  <Route
		path={route.path}
		render={props => (
		  <route.component {...props} routes={route.routes} />
		)}
	  />
	);
  }

  function PrivateRoute({ component: Com, ...rest }) {
    return (
        <Route
            { ...rest }
            render={ ({ location }) =>
                localStorage.getItem('userinfo')
                    ? <Com {...rest}/>
                    : (
                        <Redirect
                            to={ {
                                pathname: "/login",
                                state: location
                            } }
                        />
                    )
            }
        />
    );
}

export default Doc;